<template>
	<div class="body fundManage_body">
		<div class="main under_fund">
			<div class="under_fund_main table">
				<el-table ref="multipleTable" :data="tableData" border style="width: 100%">
					<el-table-column prop="id" label="序号" width="100" sortable>
						<template slot-scope="scope">
							{{ (scope.$index + 1) + (currentPage - 1) * pageSize }}
						</template>
					</el-table-column>
					<el-table-column prop="company" label="企业名称" width="200" sortable>
					</el-table-column>
					<el-table-column prop="project" label="项目名称" width="200" sortable>
					</el-table-column>
					<el-table-column prop="first_invest_date" label="首次投资日期" width="180" sortable>
					</el-table-column>
					<el-table-column prop="unit" label="币种" width="180" sortable>
						<template slot-scope="scope">
							{{ scope.row.unit == 1?'人民币':'美元' }}
						</template>
					</el-table-column>
					<el-table-column prop="invest_money" label="投资额（万元）" width="180" sortable>
					</el-table-column>
					<el-table-column prop="invest_evaluate" label="投资时估值（亿元）" width="180" sortable>
					</el-table-column>
					<el-table-column prop="invest_scale" label="投资占比" width="120" sortable>
						<template slot-scope="scope">
							{{ scope.row.invest_scale }}&nbsp;%
						</template>
					</el-table-column>
					<el-table-column prop="fair_value" label="公允价值（万元）" width="160" sortable>
					</el-table-column>
					<el-table-column prop="update_time" label="更新时间" sortable width="180">
					</el-table-column>
					<el-table-column prop="back" label="回款返本（万元）" width="180" sortable>
					</el-table-column>
					<el-table-column prop="income" label="回款收益-税后（万元）" width="200" sortable>
					</el-table-column>
					<el-table-column prop="back_scale" label="返本比例" width="120" sortable>
						<template slot-scope="scope">
							{{ scope.row.back_scale }}&nbsp;%
						</template>
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="150">
						<template slot-scope="scope">
							<div class="">
								<el-button type="text" size="small" @click="view(scope.row)">查看</el-button>
								<el-button type="text" size="small" @click="deleteFund(scope.row)">删除</el-button>
							</div>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata flex_start">
							<img src="../../images/table_nodata.png">
							<span>没有对应数据</span> 
						</div>
					</template>
				</el-table>
				<div class="" v-if="total">
					<el-pagination @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
    					</el-pagination>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getRunInvestGroupListApi,deleteRunInvestGroupListApi } from '../../config/api'

	export default {
		name: 'subFundRunInvestGroup',
		data() {
			return {
				id: '',
				currentPage: 1,
				pageSize: 10,
				total: 0,
				tableData: [],
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.getInitData()
		},
		mounted() {},
		components: {},
		methods: {
			getInitData(){
				getRunInvestGroupListApi({
					fund_id: this.id,
					page: this.currentPage,
					pageSize: this.pageSize,
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.tableData = res.data.payload.data;
						this.total = res.data.payload.total;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			view(e){
				this.$router.push({
					name: 'subFundProjectView',
					query:{
						id: e.project_id
					}
				})
			},
			deleteFund(e) {
				dialogConfirmHasbtn('确定要删除所选基金吗?').then(() => {
					deleteRunInvestGroupListApi({
						id: e.id,
					}).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm("success", res.data.message);
							this.getInitData()
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}).catch(() => {});
			},
			// 分页功能模块
			changeCurrentPage(e){
				this.currentPage = e;
				this.getInitData()
			},
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getInitData()
			},
		},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 0 0px 30px 0px;
	}
</style>


